	module.exports = {"data":{"post":{"html":"<p>Forgive me for not posting an update on my brother here... I mentioned his status on <a href=\"http://twitter.com/papascott\">Twitter</a>, but most poeple would have missed that, I guess.</p>\n<p>He was released from the hospital last Tuesday, 29 January, a week after being admitted. With two stents freeing the arteries that caused the arrest, his heart should now be better off than before. If his heart were his only health problem, he could have returned to work this week... but instead he is resuming his chemotherapy this weekend.</p>\n<p>Emotionally, he's still a mess, angry and frustrated with God and the world. That's understandable, I guess, but not particularly constructive.</p>\n<p>I returned home also last Tuesday after 5 days in Minnesota. Frauke was able to handle both son and business alone for a week, and things are now normal again at home. We've had an overnight meeting at Westerland for the two of us, a short school vacation and a late birthday party for Christopher, and a small sickness wave at work. In other words, more of the usual and less of the unusual.</p>","fields":{"title":"Pardon My Silence","date":"08 Feb 2008","url":"https://papascott.de/2008-02-08-pardon-my-silence/"}}},"pathContext":{"slug":"/2008-02-08-pardon-my-silence/","prev":{"fields":{"title":"At Least They Have Snakes","date":"08 Feb 2008","slug":"/2008-02-08-at-least-they-have-snakes/"}},"next":{"fields":{"title":"Forty-Six, and One","date":"09 Feb 2008","slug":"/2008-02-09-forty-six-and-one/"}}}}

